.tini-swiper{
    
    width: 450px;
    height: 450px;
    // position: absolute;
    // left: 30%;
    // top: 30%;
    // margin-left: -150px;
    // margin-top: -150px;
  
      .swiper-slide {
        background-position: center;
        background-size: cover;
    
        img {
            display: block;
            width: 100%;
          }
      }
    }

.tini-menu{
    justify-content: space-around;
    .tini-card {

        width: 32%;
        border-radius: 0px; 
        overflow: hidden;
        border: 0;
        border: 1px solid #e9ecef;
        transition: all 0.15s ease;
        margin-bottom: 15px;
        &:hover{
            box-shadow: 0 6px 30px rgba(0, 0, 0, 0.1),
            0 10px 8px rgba(0, 0, 0, 0.015);
        }
        .card-body{
            text-align: center;
            .card-title{
                font-family: $familyEn;
                font-size: 15px;
                color: #151414;
               
                &:before {
                        content: "";
                        position: absolute;
                        top: 62%;
                        width: 80px;
                        height: 0;
                        border-top: 1px solid #8a0d1f;
                        clear: both;
                        left: 34%;
                }
            } 
            .card-text {
                margin-bottom: 5px;
                font-family: "BodoniBook";
                color: #4E4E4E;
      
      }
        }
        .container {
            width: 88%;
            
            border-radius: 0px;
            height: 140px;
          display: flex;
         align-items: center;
          justify-content: center;
          &:hover{
            img {
                transform: scale(1.3);
    }
          }
          img {
            padding: 0px;
            /* margin-top: -40px; */
            /* margin-bottom: -40px; */
            -webkit-transition: .4s ease;
            transition: .4s ease;
            cursor: pointer;
            border-radius: 0px!important;
            transform: scale(1.15);
        
    }
    }
    }
}    


 

 
 .timing{
    font-family: $familyEn;
    text-transform: capitalize;
 }

 .book-now{
    width: 50%;
    margin: auto;
    display: flex;
    align-items: center;
    border-top: 0.5px solid #e5e5e5;
    margin-top: 40px;
    padding-top: 10px;
    .text-quote{
        font-family: $familyEn;
        margin: 0px!important;
        font-size: 18px;
    }
    button{
        min-width: 150px;
        // margin-left: -45px;
        font-family: $familyEn;
        &:hover svg{
            animation:animToRight 1s ease-in-out infinite ;
            transform-origin: left;
            transform-box: fill-box;
        }
    }
 }

 @media (min-width: 1200px){
    .modal-xl {
        max-width: 98%;
    }
 }
