.sidebar {
    position: absolute;
    width: 600px;
    height: 800px;
    background: white;
    opacity: 1;
    -webkit-transform: translateX(-600px);
    transform: translateX(-600px);
    -webkit-transition: all .8s ease;
    transition: all .8s ease;
    z-index: 55555;
    transform: translateX(-600px);
    top: 0;
    padding: 100px;

    .nav_items{
        color: #fff;
        font-size: 23px;
        font-family: $familyEnLight;
        display: flex;
        flex-direction: column;
        margin-top: 20px;
        border-top: 1px solid #dedede;
    }
    &.active{
        transform: translateX(0px);
        box-shadow: 0 0 0.9375rem 0 rgb(47 70 73 / 20%);
    }
}