.event-section-grid{
   
    display: flex;
    -webkit-box-pack: space-evenly;
    -ms-flex-pack: space-evenly;
    justify-content: flex-start;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    /* flex-direction: row; */
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    .event-grid{
        display: flex;
        justify-content: space-around;
        /* -webkit-box-orient: horizontal; */
        /* -webkit-box-direction: normal; */
        /* flex-direction: row; */
        /* -webkit-box-align: center; */
        align-items: center;
        background: rgb(245, 251, 254);
        width: 44%;
        height: 150px;
        margin: 10px;
        margin-bottom: 0px;
        margin-top: 0px;
     
    .text-grid{
        width: 49%;
        margin-right: -5%;
        z-index: 55;
        .text-title{
            font-size: 16px;
            color: #000;
            font-family: "BodoniBold";
            margin-bottom: 0px;
            text-transform: capitalize;
        }
        .body-text{
            color: gray;
            font-family:$familyEn;
            font-size:15px;
            margin-bottom: 5px;
            
        }
        .btn-outline-primary {
            color: #8a0d1f;
            border-color: #8a0d1f;
            font-size: 14px;
            transform: scale(0.90);
            &:hover{
                color: #fff;
                transform: scale(0.90);
                outline: none;
                box-shadow:none;
            }
            &:focus{
                outline: none;
                box-shadow:none;
            }
        }
    }
        .color-grid{
            background: #fff !important;
            width: 70px;
            min-height: 200px;
            margin-right: -18%;
          

        }
        .img-grid{width: 40%;
            text-align: right;
            
            img{
                width: 120px;
                -webkit-box-shadow: 0 0 0.9375rem 0 rgba(47,70,73,.2);
                box-shadow: 0 0 0.9375rem 0 rgba(47,70,73,.2);
            }
        }
    }
}