.navbar{

    height: 60px;
    background-color: $black_bg!important;
    border-bottom: 0.5px solid #747474;
    z-index: 20000;
     .navbar-nav{
        .nav-link {
            color: #c5c5c5;
            font-size: 20px;
            font-size: 15px;
    font-family: 'BodoniRegular';
    text-transform: uppercase;
       &.active {
        color:$primary!important;
      transition: all .3s ease-in-out
    }
  &:hover{
    color:$primary!important;
    transition: all .3s ease-in-out
  }    
  
  


}
        
     } 

     .more-menu{
        width: 114px;
        text-align: center;
     }
     .right-content{
        width: 114px;
        text-align: center;
        .lang-btn{
            
          }
          &:after 
          {
              
              content: "";
              background: #747474;
              position: absolute;
              bottom: 0;
              right: 114px;
              height: 100%;
              width: 0.5px;
          }
     }
     .navbar-brand {
        svg{
            width: 132px;
        }
        &:after 
        {
            
            content: "";
            background: #747474;
            position: absolute;
            bottom: 0;
            left:114px;
            height: 100%;
            width: 0.5px;
        } 
     }
     .navbar-collapse{
        .right-btns{
            display: flex !important;
            align-items: center;
            justify-content: space-around;
            .btn{
                margin-right: 10px;
                margin-left: 10px;
            }
          
        }
        .lang-btn{
            &:after 
            {
                
                content: "";
                background: #fff;
                position: absolute;
                bottom: 0;
                right: 60px;
                height: 100%;
                width: 1px;
            } 
          }
     }
     .burgerMenu{
        &:after 
        {
            
            content: "";
            background: #fff;
            position: absolute;
            bottom: 0;
            left: 60px;
            height: 100%;
            width: 1px;
        } 
     }
      
}

