$familyAR: "NeoSansArabicRegular";

@mixin fontFace($family, $src, $style: normal, $weight: normal) {
  @font-face {
    font-family: $familyAR;
    src: url("#{$src}.eot"), // IE9 compat
    url("#{$src}.ttf") format("truetype");
    font-style: $style;
    font-weight: $weight;
  }
}

@include fontFace("NeoSansArabicRegular", "../fonts/NeoSansArabicRegular");

 

