//If you want to target a device
@media only screen and (max-width: 480px){ 
     .tini-menu{
        flex-direction: column;
        .tini-card{
            width: 100%!important;
        }
    }
    .about-section{
        margin: auto;
        overflow-x: hidden;
    }
    .event-section .event-items{
        justify-content: center;
    }

    main {
       
        margin-left: 10px!important;
        margin-right: 10px!important;
    }
 
    .about-section{
        .imgs-slider{
            figure{
                margin: 0px!important;
                float: none;
                text-align: center;
                .oval{
                    display: none;
                }
                img{
                    width: 350px !important;
                    height: 400px !important;
                }
                &:after {
                  
                    left: 1%;
                    bottom: 10%;
                 
                }
            }
        }
       .about-items {
        .about-item {
            .about-body {
                padding: 5px;
            }
        }
       }
    }
    .footer {
        .container {
            padding-left: 10px!important;
            padding-right: 10px!important;
            text-align: center!important;
        }
        .middle{
             div {
              
                justify-content: center;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
            }
        }
        .quick-links {
            
            align-items: center;
        }

        .newsletter {
           
            -ms-flex-pack: end;
            justify-content: center;
        }
    } 
    .text-right {
        text-align: center;
    }
    .book-section {
        height: auto!important;
       .items {
        .item {
            border-bottom: 1px solid rgba(246,246,246,.2901960784);
            text-align: center;
            margin-bottom: 15px;
            border-right: none;
        }
       }
    }
    .services-section {
        height: auto;
        .items {
            .item {
                border-bottom: 1px solid rgba(246,246,246,.2901960784);
                border-right: none;
            }
        }
    }
    .event-section {
        .events-imgs{
            figure {
                float: none;
                text-align: center;
                img {
                  
                    width: 300px !important;
                    height: 350px !important;
                }
                .oval{
                    display: none!important;
                }
            }
        }
    }
    .hero-slider {
        .logo-middle{
            bottom: 350px;
        }
        .info-section { 
            z-index: 17000;
            bottom: 25px;
            left: 0; 
            height: auto;
            width: 100%;
            color: #fff;
            margin: auto;
            padding-right: 1%;
    padding-left: 1%;
    padding-bottom: 40px;
          .items {
            .item{
                
                    border-bottom: 1px solid rgba(246,246,246,0.2901960784);
                    text-align: left;
                    margin-bottom: 10px;
                    border-right: none;
                 &:last-child{
                    border-bottom:none;
                    
                 }
            }
          }
        }
       .swiper-pagination {
            
            width: 50%;
            top: 93%;
            left: 36%;
        }
    }
    .services-slider {
        .item {
            .slide-img {
                figure{
                    margin: auto;
                    img {
                        width: 402px!important;
                        height: 301px!important;
                    }
                    &:after{
                       
                            display: none;
                        
                    }
                }
              .rect-red {
                  
                    display: none;
                }
            }
        }
       .services {
        .swiper-wrapper {
            height: auto;
        }
       }
    }
    .p-100 {
        padding: 35px!important;
    }


    .event-section {
        .event-items {
            .event-item {
       
                flex-direction: column!important;
                text-align: center;
                .event-number {
                     
                    width: 100%;
                    border-bottom: 1px solid #c9c9c9;
                    padding-right: 0;
                    padding-bottom: 20px;
                    border-right: none;
                }
                .event-image {
                    width: 100%;}
            }
        }
    }
}
