/********** responsive navbar ***********/
#click {
    display: none;
}

.menu {
    display: none;
}
@media only screen and (max-width:480px) {

    .hero-slider .info-section .items {
        
        flex-direction: column!important;
    }
    .text-left {
        text-align: center!important;
    }
    .event-section-grid {
       
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .event-grid {
            width: 100%!important;
            margin-bottom: 10px!important;
            margin-top: 10px!important;
        }
    }
    .event-page .card--vertical {
       display: none;
    }
    html {
        font-size: 85%;
    }

    header {
        height: 100vh;
    }
    #navbarSupported li {
        padding: 0.2rem 1.1rem;
    }
    .menu {
        display: block;
        font-size: 1.5rem;
        font-weight: bold;
        color: #fff;
    }
    #navbarSupported {
        width: 100%;
        // height: 100vh;
        position: absolute;
        display: flex;
        flex-direction: column;
        text-align: center;
        // top: 4rem;
        // left: -100%;
   top: -20rem;
 left: 0%;
        form{
            display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    text-align: center; 
    align-items: center;
 
        }
    }
    #click:checked~#navbarSupported {
        left: 0%;
        top: 4rem;;
        transition: all 0.5s ease;
        z-index:9999999;
        background: #111010a1;
    -webkit-backdrop-filter: saturate(180%) blur(5px);
    backdrop-filter: saturate(180%) blur(5px);
    padding-bottom: 20px;
    }

}