.about-section{
          
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .section-title{
        font-family: $familyEnBold;
        font-size: 23px;
        text-transform: uppercase;
       
        &:before {
            display: inline-block;
    content: "";
    border-top: 4px solid $primary;
    width: 3rem;
    margin: 0 1rem 0 0;
        }

    }
    .imgs-slider{



        figure{
            position: relative;
            float: left;
            margin: 0 3rem 1rem 0;
            &:after {  
                  content: "";
                position: absolute;
                left: -4%;
                bottom: 10%;
                background: $primary;
                width: 200PX;
                height: 80%;
                z-index: -1;
            }
            .oval{
                position: absolute;
                width: 550px;
                left: -35px;
                z-index: -5;
                top: 40%;
            }
            img{
                width: 438px;
                height: 574px;
            }
        }
        
            
            .about-swiper {
                position: relative;
        width: 100%;
        height: auto;
        .swiper-wrapper{
            height: auto;
            .swiper-slide {
                text-align: center;
                font-size: 18px;
                background: transparent;
                height: auto;
                align-items: center;
                display: block;
                width: 100%;
                padding: 0px;
                margin: 0px;
                 
              }
        }
       
      }
  
         
    }
.about-items{

    padding: 10px;
    margin-top: 20px;
    .about-item {
        background-color: #fff0;
        border-bottom: 1px solid rgba(35,31,32,.125);
      margin-top: 10px;
        .about-header{
            background-color: #fff0;
            font-family: $familyEnBold;
            font-size: 18px;
            text-transform: uppercase;
              color:$primary;
        } 
        .about-body{
            font-family:$familyEn;
           font-size:$text-size;
            color:$text-color;
            padding: 20px;
            padding-top: 5px;
        }
    }
}
 
