.input-number {
    width: 80px;
    padding: 0 12px;
    vertical-align: top;
    text-align: center;
    outline: none;
  }
  
  .input-number,
  .input-number-decrement,
  .input-number-increment {
     
    height: 37px;
    background-color: rgba(0,0,0,0);
    border: none;
    border-radius: 0px;
    color: #fff;
    border-bottom:1px solid rgba(255,255,255,.2901960784);
    user-select: none;
  }
  
  .input-number-decrement,
  .input-number-increment {
    display: inline-block;
    width: 30px;
    line-height: 38px;
    background: transparent;
    color: $primary;
    text-align: center;
    font-weight: bold;
    cursor: pointer;
  }
  .input-number-decrement:active,
  .input-number-increment:active {
    background: #8a0d1f54;
  }
  
  .input-number-decrement {
    border-right: none;
    border-radius: 0px
  }
  
  .input-number-increment {
    border-left: none;
    border-radius: 0px;
  }

