    .slider-nav2{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        position: absolute;
        // margin-top: -200px;
        z-index: 1200;
        width: 80%;
        justify-content: flex-end;
    }
    .slider-arr2{
        -webkit-box-flex: 0;
        -ms-flex: 0 0 auto;
        flex: 0 0 auto;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: center;
        width: 80px;
        // margin-right: 27px;
        // background: $primary;
        height: 45px;
        /* padding: 5px; */
        align-items: center;
        .prev2, .next2{
            background: $primary;
             padding: 6px;
        }
        .prev2.swiper-button-disabled{
            background:#d0d0d0!important;
        }
        .next2.swiper-button-disabled{
            background:#d0d0d0!important;
        }
        .next2{
            &:hover{
                svg{
                    animation: animToRight 1s ease-in-out infinite;
                    transform-origin: left;
                    transform-box: fill-box;
                    }
            }
        }
        .prev2{
            &:hover{
                svg{
                    animation: animToLeft 1s ease-in-out infinite;
                    transform-origin: left;
                    transform-box: fill-box;
                    }
            }
        }
    }
 }
 
  
  
  
 
  
