$font-family-primary: 'Fjalla One', sans-serif;
$font-family-secondary: 'Noto Sans', sans-serif;

 

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 99999;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(14, 29, 51, 0.8)), to(rgba(14, 29, 51, 0.2)));
    background: linear-gradient(to bottom, #00000085, #0000008c);
}

 

/* --------------------------------
Masking
-------------------------------- */
.slide-content {
  .title,
  .caption,
  .btn{
    position: relative;
    opacity: 0;
    animation-fill-mode: forwards;
  }
}

.slide-content.mask {
  .mask{
    position: relative;
    overflow: hidden;
  }

  .title{
    animation-duration: 0.4s;
    animation-delay: 0.7s;
    animation-name: slide-up;
    animation-fill-mode: backwards;
    opacity: 1;
   }

  .divider {
    display: inline-block;
    position: relative;
    margin: 5px auto;
    height: 2px;
    width: 60%;
    background-color: #ffffff;
    animation: divider-mask 1s 0.3s both;
  }

  .caption,
  .btn{
    animation-duration: 0.4s;
    animation-delay: 0.7s;
    animation-name: slide-down;
  }
}

@-webkit-keyframes slide-up {
  0% {
    opacity: 0;
    -webkit-transform: translateY(100%);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@-moz-keyframes slide-up {
  0% {
    opacity: 0;
    -moz-transform: translateY(100%);
  }

  100% {
    opacity: 1;
    -moz-transform: translateY(0);
  }
}

@keyframes slide-up {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@-webkit-keyframes slide-down {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@-moz-keyframes slide-down {
  0% {
    opacity: 0;
    -moz-transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    -moz-transform: translateY(0);
  }
}

@keyframes slide-down {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@-webkit-keyframes divider-mask {
  0%,
  100% {
    transform: scaleX(0);
  }

  40%,
  60% {
    -webkit-transform: scaleX(1);
  }
}

@-moz-keyframes divider-mask {
  0%,
  100% {
    transform: scaleX(0);
  }

  40%,
  60% {
    -moz-transform: scaleX(1);
  }
}

@keyframes divider-mask {
  0%,
  100% {
    transform: scaleX(0);
  }

  40%,
  60% {
    transform: scaleX(1);
  }
}
